/*------------------------------------------------------------------
[10. Modals]
*/
.modal {
  .close:focus {
    outline: 0;
  }
  .modal-dialog {
    transition: all .2s !important;
     &.modal-xl {
      .modal-content {
        height: initial;
        width: initial;
      }
    }
  }
  .modal-content {
    width: 851px;
    height: 462px;
    border-radius: 0;
    border: none;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    padding: 30px 40px 40px 40px;
  }
  .modal-header {
    border-bottom: 0;
    padding: 0;
    p {
      color: #8b91a0;
    }
  }
   button.close {
    font-size: 25px;
    text-shadow: none;
    opacity:1;
    color:#a5b526;
  }
  .modal-body {
    box-shadow: none;
    padding: 0;
    white-space: normal;
  }
  .modal-footer {
    border-top: none;
    box-shadow: none;
    margin-top: 0;
    padding: 25px;
    padding-top: 0;
  }
  .drop-shadow {
    box-shadow: 0 0 9px rgba(191, 191, 191, .36) !important;
  }
 
  &.fade {
    opacity: 1 !important;
    &.stick-up {
      .modal-dialog {
        .translate3d(0, -100%, 0);
        margin-top: -5px;
      }
      &.in {
        .modal-dialog {
          .translate3d(0, 0, 0);
        }
      }
    }

    &.slide-up {
      height: 100%;
      top: auto;

      &.in {
        .modal-dialog {
          .translate3d(0, 0, 0);
        }
      }
      &.disable-scroll {
        overflow: hidden;
      }
      .modal-dialog {
        display: table;
        height: 100%;
        .translate3d(0, 75%, 0);
      }
      .modal-content-wrapper {
        display: table-cell;
        vertical-align: middle;
      }
    }
    &.center-scale {
      .modal-dialog {
        display: table;
        height: 100%;
      }
      .modal-content {
        display: table-cell;
        vertical-align: middle;
        border: 0;
        background: transparent;
        .modal-header {
          border-bottom: 0px;
        }
      }
      &.in {
        .modal-dialog {
          opacity: 1;
          .scale(1);
        }
      }
      .modal-dialog {
        opacity: 0;
        .scale(0.6);
      }
    }
    &.fill-in {
      background-color: rgba(255, 255, 255, 0);
      .transition(background-color .3s);
      & > .close {
        margin-top: 20px;
        margin-right: 20px;
        opacity: 0.6;
        & > i {
          font-size: 16px;
        }
      }
      .modal-dialog {
        display: table;
        height: 100%;
      }
      .modal-content {
        display: table-cell;
        vertical-align: middle;
        border: 0;
        background: transparent;
        .modal-header {
          border-bottom: 0px;
        }
      }
      &.in {
        background-color: rgba(255, 255, 255, 0.85);
        .modal-dialog {
          opacity: 1;
          .scale(1);
        }
      }
      .modal-dialog {
        opacity: 0;
        .scale(0.6);
      }
    }
    &.slide-right {
      .close {
        position: absolute;
        top: 0;
        right: 0;
        margin-right: 10px;
        z-index: 10;
      }
      &.in {
        .modal-dialog {
          .translate3d(0, 0, 0);
        }
      }
      .modal-dialog {
        position: absolute;
        right: 0;
        margin: 0;
        height: 100%;
        .translate3d(100%, 0, 0);
        .modal-content-wrapper {
          height: 100%;
        }
        .modal-content {
          height: 100%;
        }
      }
      .modal-content {
        border-radius: 0px;
      }
    }
  }
}

.fill-in-modal {
  .modal-backdrop {
    background-color: transparent;
  }
}

.modal-backdrop {
  opacity: 0;
  .transition(opacity 0.2s linear);
  &.in {
    z-index: 999;
    opacity: 0.9;
    background: #d3d3d3;
  }
}


#topmenu.modal {
    background: #401b21;
    opacity: .95!important;
    
    .modal-content-wrapper{
      vertical-align: top;
      padding-top: 125px;
    }
    
    .modal-dialog .modal-content {
        background: none;
        padding: 82px 0px 30px;
    }
    button.close {
        color: #d3e068;
        &:hover {
            color: #7db701;
        }
    }
    .flex-menu {
        display: inline-flex;
        flex-wrap: wrap;
        width: 100%;
        justify-content: space-between;
    }
    .flex-menu-item {
        margin: 0 10px;
        font-size: 13px;
        position: relative;
        > li {
            height: 32px;
            margin-top: 7px;
            margin-bottom: 10px;
        }
        li{
            color: #8e8082;  
            
        }
        li a, button {
            color: #d3e068;
        }
        li a:hover, button:hover {
            color: #7db701;
        }
        ul {
          position: absolute;
          li {
            margin-bottom: 8px;
          }
        }
        &:first-child {
          width:10px;
          margin-left: -32px;
        }
        &:last-child {
          text-align: right;
          margin-right: -1px;
          ul {
            position: absolute;
            right: 0;
            width: 200px;
          }
        }
    }
}




.modalpage.modal {
    &.fade.slide-up {
        height: auto;
        top: 30px;
        bottom: 30px;
    }
    

    .modal-content {
        height: 100vh!important;
        max-height: ~"calc(100vh - 60px)";
        padding: 29px 40px 70px 40px;
        
        .modal-body {
            height: 95%;
            overflow: auto;
            font-family: "Roboto";
            margin-top: 3px;
            padding-top: 16px;
            
            p {
                font-size: 15px;
            }
            &::-webkit-scrollbar {
                width: 8px;
            }
            &::-webkit-scrollbar-thumb {
                border-radius: 10px;
                -webkit-box-shadow: inset 0 0 20px 20px #a5b526;
            }
            &::-webkit-scrollbar-track {
                -webkit-box-shadow: inset 0 0 5px 1px #edeeed;
                border-radius: 10px;
            }
        }
    }
}
#cart-modal {
    .modal-content {
        background-color: #eeefee;
        > .col-md-8,
        > .col-md-4 {
            margin-top: 9px;
        }
    }
    .btn-warning{
        margin-top: 24px;
    }
}



/* Responsive Handlers: Modals
------------------------------------
*/

@media (max-width: 768px) {
  .modal {
    &.fill-in {
      .modal-dialog {
        width: calc(~'100% - 20px');
      }
    }
    &.slide-up {
      .modal-dialog {
        width: calc(~'100% - 20px');
      }
    }
  }
}

@media (min-width: 768px) {
  .modal {
    &.stick-up {
      .modal-dialog {
        margin: -5px auto;
      }
    }
    &.slide-up {
      .modal-dialog {
        margin: 0 auto;
      }
    }
    &.fill-in {
      .modal-dialog {
        margin: 0 auto;
      }
    }
    .modal-content {
      box-shadow: none;
    }
  }
}

@media (min-width: 1200px) {
  .modal-xl {
    width: 1170px;
  }
  #topmenu.modal {
    .modal-dialog.modal-lg .modal-content {
        width: 1160px;
    }
    .flex-menu {
        flex-wrap: nowrap;
    }
  }  
}
